}

nav{
  color: azure;
  background-color: rgb(58, 58, 58);
  font-family: 'Montserrat', sans-serif;
  text-decoration: none;
  padding-right: 45px;
}

img.logoHeader{
  border-radius: 20px;
  object-fit: cover;
  width: 200px;
  height: 60px;
}

nav{
  background-color: rgb(58, 58, 58);
  border-radius: 22px;
  display: flex;
  align-items: center;
  text-align: center;

}

nav {
    color: azure;
    background-color: rgb(58, 58, 58);
    font-family: 'Montserrat', sans-serif;
  
  }
  
  nav > a {
    margin: 0;
    margin-left: 25px;
    padding: 10px 20px;
    color: azure;
    background-color: rgb(58, 58, 58);
    text-decoration: none;
  }
 
  nav > a.active {
    background-color: rgb(49, 49, 49);
    border-radius:  50px;
    text-decoration: none;
  }
  
.List{
    display: flex;
    height: 160px;
    flex-direction: row;
    background-color: rgb(65, 65, 65);;
    width: 80%;
}

.List > img{
    width: 100px;
    margin-right: 30px;
}

.List > p{
    color: aliceblue;
    font-family: 'Montserrat', sans-serif;
    background-color: rgb(65, 65, 65);;
    font-size: 25px;
}

.movieTitle{
    margin-right: 15px;
    height: 30px;
}

p.releasedYear{
    font-size: 18px;
    height: 23px;
}
.top{
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.bigCard{
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    width: 95%;
}


.bigCard > h1   {
    color: aliceblue;
    font-family: 'Montserrat', sans-serif;
    font-size: 60px;
    margin-bottom: 100px;
}
